<template>
  <div class="clearfix">
      <!--Menu-->
      <div class="menuContent h100">
        <span class="menuTitle">功能列表</span>
          <ul class="FuncMenu">
             <li v-on:click="$_imgGray(1)">灰度变换-浮点算法</li>
             <li v-on:click="$_imgGray(2)">灰度变换-整数算法</li>
             <li v-on:click="$_imgGray(3)">灰度变换-移位算法</li>
             <li v-on:click="$_imgGray(4)">灰度变换-平均值算法</li>
             <li v-on:click="$_imgGray(5)">灰度变换-仅取绿色</li>
          </ul>
      </div>
      <!--Image-->
      <div class="imgContent h100">
            <input class="v-center" type="file" v-on:change="$_imgFileChange" ref="eleImgFile">
            <canvas class="imgShow" v-bind:width="imgWidth" v-bind:height="imgHeight" ref="eleImgCanvas"></canvas>
      </div>
  </div>
</template>
<script>
    export default {
        name: 'Li',
        created(){
            this.$emit('comInit',window.location.hash)
        },
        data:function(){
            return {
                imgData:null,
                imgNowData:'',
                imgWidth:500,
                imgHeight:500,
                currentFuction:''
            }
        },
        methods:{
            $_imgFileChange:function(){
                var _this=this;
                let files =this.$refs.eleImgFile.files;
                if(files.length>0){
                    let file = files[0];
                    if(!(/^image\//).test(file.type)){
                        alert('请选择图像文件');
                        return;
                    }
                    let fr=new FileReader();
                    fr.readAsDataURL(file);
                    fr.onload=function(res){
                        _this.imgData=res.target.result;
                        _this.imgNowData=_this.imgData;
                        _this.currentFuction='';
                        _this.$_showImg();
                    }
                }
            },
            $_showImg:function(callback){
                var _this=this;

                ;

                let img=new Image();
                img.src=_this.imgNowData;
                img.onload=function(res){
                    let _width=res.target.width;
                    let _height=res.target.height;
                    let ratio=1;
                    if(_width>500) ratio=500/_width;
                    if(_height*ratio>500) ratio=500/_height;

                    _width=_width*ratio;
                    _height=_height*ratio;


                    _this.$refs.eleImgCanvas.getContext('2d').clearRect(0,0,_this.imgWidth,_this.imgHeight)

                    let eleCanvas=_this.$refs.eleImgCanvas;
                    let ctx=eleCanvas.getContext('2d');
                    ctx.drawImage(img,(_this.imgWidth-_width)/2,(_this.imgHeight-_height)/2,_width,_height);
                    
                    if(typeof callback =='function')
                        callback();
                }
            },
            $_imgGray:function(index){
                var _this=this;
                index=!index?1:index;
                let _currentFunction='imgGray_'+index;
                if(!_this.imgData||_this.currentFuction==_currentFunction)
                    return;
                _this.$_showImg(function(){
                    let eleCanvas=_this.$refs.eleImgCanvas;
                    let ctx=eleCanvas.getContext('2d');
                    let _img256=ctx.getImageData(0,0,_this.imgWidth,_this.imgHeight);
                    for (var i=0;i<_img256.data.length;i+=4)
                    {
                        var R = _img256.data[i]; //R(0-255)
                        var G = _img256.data[i+1]; //G(0-255)
                        var B = _img256.data[i+2]; //G(0-255)
                        var Alpha = _img256.data[i+3]; //Alpha(0-255)

                        let gray=0;
                        if(!index||index==1){
                            gray = R*0.299 + G*0.587 + B*0.114;//浮点算法
                        }
                        else if(index==2){
                            gray = (R*299 + G*587 + B*114 + 500) / 1000;　//整数算法
                        }
                        else if(index==3){
                            gray =(R*76+G*151+B*28)>>8;//移位算法
                        }
                        else if(index==4){
                            gray = (R+G+B)/3;//平均值算法
                        }
                        else{
                            gray = G;//仅取绿色
                        }
                        
                        _img256.data[i] = gray;
                        _img256.data[i+1] = gray; 
                        _img256.data[i+2] = gray; 
                        _img256.data[i+3] = Alpha; 
                    }
                    _this.$refs.eleImgCanvas.getContext('2d').clearRect(0,0,_this.imgWidth,_this.imgHeight)
                    ctx.putImageData(_img256,0,0,0,0,_this.imgWidth,_this.imgHeight);

                    _this.currentFuction=_currentFunction;
                });
                
            }
        }
    }
</script>
<style scoped>
    .menuTitle{
        color: wheat;
        text-align: left;
        line-height: 40px;
        margin-left:20px;
        font-weight: 500;
        font-size: 20px;
    }
    .menuContent{
        position: absolute;
        top:31px;
        left: 0;
        bottom: 0;
        width: 200px;
        background-color: #333;
        text-align: left;
        display: inline-block;
    }
    .FuncMenu{
        color:white;
        text-align: center;
    }
    .FuncMenu li{
        cursor: pointer;
        line-height:36px;
        text-align: left;
        padding-left: 35px;
    }
    .FuncMenu li:hover{
        background-color: #aaa;
        color: #333;
    }

    .imgContent{
        position: absolute;
        top:31px;
        bottom: 0;
        left: 220px;
        right: 20px;
        display: inline-block;
        overflow: hidden;
    }
    .imgShow{
        position: absolute;
        top:50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        /*max-width: 500px;
        max-height: 500px;*/
    }

</style>



